import { Injectable } from '@angular/core';
import { select, NgRedux } from '@angular-redux/store';
import { Observable } from 'rxjs';
import { Answer } from './api/question-api.model';
import { QuestionApiDataApiActions } from './api/question-api.actions';
import { AppState } from '../../store/model';

@Injectable({
    providedIn: 'root'
})
export class QuestionService {
    @select(['questionApi', 'data'])
    public answers$: Observable<Answer[]>;

    @select(['questionApi', 'loading'])
    public loading$: Observable<boolean>;

    @select(['questionApi', 'success'])
    public success$: Observable<boolean>;

    @select(['questionApi', 'error'])
    public error$: Observable<boolean>;

    constructor(
        private questionActions: QuestionApiDataApiActions,
        private store: NgRedux<AppState>
    ) {}

    public requestQuestion(parameters): Observable<Answer[]> {
        this.questionActions.loadData(parameters);
        return this.answers$;
    }

    public saveQuestionValue(lastQuestion): void {
        this.questionActions.questionSave(lastQuestion);
    }

    public getLastQuestion() {
        return this.store.getState()['questionApi'].lastQuestion;
    }
}
