import { Component, ViewChild, AfterViewInit } from '@angular/core';
import { Observable } from 'rxjs';
import { QuestionService } from './question.service';
import { FORM_CONFIG } from './question-form.config';
import { NormalizeTime } from '../../services/NormalizeTime';
import { Th2GenericFormComponent } from '../../utils/forms/th2-generic-form.component';
import { Answer } from './api/question-api.model';

@Component({
    selector: 'question-component',
    templateUrl: 'question.component.html'
})
export class QuestionComponent implements AfterViewInit {
    public formConfig = FORM_CONFIG;
    public answers$: Observable<Answer[]> = this.questionService.answers$;
    public success$: Observable<boolean> = this.questionService.success$;
    public loading$: Observable<boolean> = this.questionService.loading$;
    public error$: Observable<boolean> = this.questionService.error$;

    @ViewChild(Th2GenericFormComponent)
    private th2GenericFormComponent: Th2GenericFormComponent;

    constructor(private questionService: QuestionService, private normalizeTime: NormalizeTime) {}

    public ngAfterViewInit(): void {
        this.loadLastQuetionForm();
    }

    public onSubmit(params) {
        const parameters = { ...params.questionForm };
        if (parameters.dateTo && parameters.dateTo instanceof Date) {
            parameters.dateTo = this.normalizeTime.toBackend(parameters.dateTo);
        }

        if (parameters.dateFrom && parameters.dateFrom instanceof Date) {
            parameters.dateFrom = this.normalizeTime.toBackend(parameters.dateFrom);
        }
        this.questionService.saveQuestionValue(parameters);
        this.questionService.requestQuestion(parameters);
    }

    protected getTextDependingOnIfAQuestionWasAsked(): string {
        const lastQuestion = this.questionService.getLastQuestion();
        if (lastQuestion) {
            return 'Answer not found. Please try with another question?';
        } else if (!lastQuestion) {
            return 'What is your question?';
        }
    }

    private loadLastQuetionForm(): void {
        const lastQuestion = this.questionService.getLastQuestion();
        if (!lastQuestion) {
            return;
        }

        const questionFormControl = this.th2GenericFormComponent.formGroup.controls['questionForm'];
        questionFormControl.patchValue({ question: lastQuestion.question });
        if (lastQuestion.dateTo) {
            questionFormControl.patchValue({ dateTo: new Date(lastQuestion.dateTo) });
        }

        if (lastQuestion.dateFrom) {
            questionFormControl.patchValue({ dateFrom: new Date(lastQuestion.dateFrom) });
        }
    }
}
